.page-header__logo {
  width: 320px;
  margin: 0 auto;
  position: relative;
  height: 56px;
}

@media (max-width: @tablet-width) {
  .page-header__logo-link {
    position: absolute;
    right: 109px;
    width: 102px;
    height: 83px;
    z-index: 20;
  }
}

.page-header__invitation {
  position: relative;
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-color: #e2eff7;
  background-position: center;

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-back-mobile@2x.jpg");
    background-size: 768px auto;
  }
}

.page-header__invitation::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 320px;
  height: 26px;
  background-image: url("../img/bg-intro-triangle-mobile.svg");
  box-shadow: 1px 0 0 #ffffff inset, -1px -1px 0 #ffffff inset;
}

.page-header__invitation-img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-top: 90px;
  padding-bottom: 85px;
  width: 281px;
  height: 214px;
}

.page-header--photo {
  .page-header__invitation::after {
    top: 133px;
  }
}

.page-header__invitation--photo {
  background-position: -225px -231px;

  .page-header__invitation-img {
    width: 230px;
    height: 40px;
    padding-top: 52px;
    padding-bottom: 67px;
  }
}

@media (min-width: @tablet-width) {
  .page-header {
    position: relative;
  }

  .page-header__logo-link {
    position: absolute;
    right: 109px;
    top: -56px;
    width: 102px;
    height: 83px;
    z-index: 20;
  }

  .page-header__logo {
    height: 0;
  }

  .page-header__invitation::after {
    content: "";
    position: absolute;
    top: 462px;
    width: 768px;
    height: 57px;
    background-image: url("../img/bg-intro-triangle-tablet.svg");
    border-bottom: 2px solid #ffffff;
  }

  .page-header__invitation {
    background-image: url("../img/bg-back-tablet@1x.jpg");
    background-repeat: no-repeat;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-back-tablet@2x.jpg");
      background-size: 768px auto;
    }
  }

  .page-header__invitation-img {
    display: block;
    padding-top: 78px;
    padding-bottom: 91px;
    width: 460px;
    height: 350px;
  }

  .page-header--photo {
    .page-header__invitation-img {
      padding-top: 96px;
      padding-left: 3px;
      padding-bottom: 115px;
      width: 460px;
      height: 80px;
    }

    .page-header__invitation::after {
      top: 235px;
    }
  }

  .page-header__invitation--photo {
    background-position: right -226px;
  }
}

@media (min-width: @desktop-width) {
  .page-header__logo-link {
    right: 66px;
    width: 188px;
    height: 96px;
  }

  .page-header__logo-img {
    width: 188px;
    height: 96px;
  }

  .page-header__logo::after {
    right: 42%;
    width: 188px;
    height: 96px;
    background-image: url("../img/logo-sedona-desktop.svg");
  }

  .page-header__invitation::after {
    top: 89%;
    width: 1200px;
    background-image: url("../img/bg-intro-triangle-desktop.svg");
  }

  .page-header__invitation {
    background-image: url("../img/bg-back-desktop@1x.jpg");

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-back-desktop@2x.jpg");
      background-size: 1200px auto;
    }
  }

  .page-header__invitation-img {
    padding-top: 78px;
    padding-bottom: 90px;
  }

  .page-header--photo {
    margin-bottom: 110px;

    .page-header__invitation-img {
      padding-top: 104px;
      padding-bottom: 119px;
    }

    .page-header__invitation::after {
      top: 246px;
    }
  }

  .page-header__invitation--photo {
    background-position: right -216px;
  }
}
